import styled, { css } from 'styled-components';

export const StyledAvatarComponent = styled.div`
  display: flex;
  gap: 1rem;
  border-radius: 50%;
  overflow: hidden;

  img {
    width: 3rem;
    height: 3rem;
    object-fit: cover;

    ${(props) =>
      props.size === 'sm' &&
      css`
        width: 4rem;
        height: 4rem;
      `}

    ${(props) =>
      props.size === 'ml' &&
      css`
        width: 4rem;
        height: 4rem;
      `}

    ${(props) =>
      props.size === 'lg' &&
      css`
        width: 5rem;
        height: 5rem;
      `}
  }
`;
